import {CHANGE_VIEW} from "../actions/nav";

const initState = {
  id: 'list-all',
};
const listUser = [
  {
    id: "doanh",
    mssv: "1235"
  },
]
export default (state = initState, action) => {
  console.log(state);
  switch (action.type) {
    case "@@redux/INIT":
      return initState;
    case CHANGE_VIEW:
      return Object.assign({}, state, {id: action.id});
    default:
      return state;
  }
}











