import React, {Component} from 'react';
import Test2 from './test2';


export default class Test extends Component {

  constructor(props) {
    super(props);
    this.state = {
      num: 0,
    }
  }

  incr = () => {
    this.setState({num: this.state.num + 1});
  }


  render() {
    return (
      <div className="">
        <p>{this.state.num}</p>
        <Test2 num = {this.incr} />
      </div>
    );
  };

}


